import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import Hoc from '../../hoc';
import {connect} from 'react-redux';
import {getingDay} from '../../../actions';
import './Header.scss';
import Burgericon from './img/burgericon.png';
import Logo from './img/logotype.png';
import Online from './img/onliner.png';
import Notificationn from './img/notification.png';
import Settinhs from './img/settinhs.png';
import Exitttein from './img/exitterout.png';


class Header extends Component{
    constructor(props){
        super(props)
        this.state={
            data:''
        }
    }
    componentDidMount(){
        this.keywordCloser()
        // document.getElementById('homeclicker').click()
        window.addEventListener('resize',()=>{
            document.getElementById('roketstarts').style.display="none";
            document.getElementById('lolkek').classList.remove('ortographick');
            document.getElementById('svgcloser').style.display = 'none';
            document.getElementById('svgopen').style.display="block"
        })
        let arraymonth = ['January','February','March','April','May','June','July','August','September','October','November','December'];
        let arratdatweek =  ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];
        if(localStorage.getItem('jwt')!==null && localStorage.getItem('jwt')!== "undefined"){
            document.getElementById('narmantia').style.display = 'none'
            const{mainService}=this.props
            mainService.getUserDetalis()
            .then(response=>{
              
           
                document.getElementById('aaaason').classList.remove('kuker')
                // document.getElementById('classica').innerHTML = ''
                this.props.getingDay(response.data.userdata.invites.length)
                let div_for_circle = document.createElement('div')
                let img_for_circle = document.createElement('img')
                img_for_circle.src  = `${Online}`
                img_for_circle.alt = "Online"
                img_for_circle.classList.add('cicleonliner_toggler')
                div_for_circle.classList.add('circle_for_toggglerino')
                div_for_circle.innerHTML = response.data.userdata.username[0].toUpperCase()
                div_for_circle.appendChild(img_for_circle)
                

                // 123
                let div_top_content = document.createElement('div')
                let div_cricle_for_dropdown = document.createElement('div')
                let p_emailer = document.createElement('p')
                let p_emailernextst = document.createElement('p')
                

                let div_log_outbut = document.createElement('div')
                let div_settinhs = document.createElement('div')
                let img_settinhs = document.createElement('img')
                let setting_text = document.createElement('p')
                div_settinhs.classList.add('diverSettinghsd')
                div_log_outbut.classList.add('circlingtopperschancell')
                setting_text.innerHTML = 'Profile Settings'
                
                img_settinhs.src = `${Settinhs}`
               
                
                div_settinhs.appendChild(img_settinhs)
                div_settinhs.appendChild(setting_text)
                document.getElementById('erevanino').appendChild(div_settinhs)
               

                let div_exiting = document.createElement('div')
                let img_exitinh = document.createElement('img')
                let exit_texst = document.createElement('p')
                div_exiting.classList.add('diverSettinghsd1')
                exit_texst.innerHTML = 'Log Out'
                img_exitinh.src = `${Exitttein}`
                div_exiting.appendChild(img_exitinh)
                div_exiting.appendChild(exit_texst)
                div_log_outbut.appendChild(div_exiting)
                div_exiting.addEventListener('click',()=>{
                    this.funlogoutfrom()
                })


                p_emailernextst.innerHTML = response.data.userdata.email
                p_emailer.innerHTML = response.data.userdata.username
                p_emailernextst.classList.add('p_emailernextststyle')
                p_emailer.classList.add('peteoremail')


                div_cricle_for_dropdown.classList.add('circlingtoppers')
                div_top_content.classList.add('dro12pdowntopmenu')
                div_cricle_for_dropdown.innerHTML  = response.data.userdata.username[0].toUpperCase()
                div_top_content.appendChild(div_cricle_for_dropdown)
                div_top_content.appendChild(p_emailer)
                div_top_content.appendChild(p_emailernextst)
                document.getElementById('ermula').appendChild(div_top_content)
                document.getElementById('tarkov').appendChild(div_log_outbut)
                // 123


                div_for_circle.addEventListener('click',()=>{

                    if(document.getElementById('tarkov').classList.contains('torinduboshit')){
                        document.getElementById('tarkov').classList.remove('torinduboshit')
                    }
                    else{
                        document.getElementById('tarkov').classList.add('torinduboshit')
                        
                    }
                })





                document.getElementById('nossssaa').appendChild(div_for_circle)
                // document.getElementById('classica').appendChild(div_for_notification)
            })
            if(localStorage.getItem('jwt')!==null && localStorage.getItem('jwt')!== "undefined" ){
                mainService.getUserDetalis().then(response=>{
                })
                .catch(errr=>{
                    localStorage.removeItem('jwt')
                })
            }
        }
        let dataforshow = new Date();
        document.getElementById('arkvarden').innerHTML = `${arratdatweek[dataforshow.getDay()]} ${dataforshow.getDate()} ${arraymonth[dataforshow.getMonth()]} ${dataforshow.getFullYear()}`
        function Timerr(){
            document.getElementById('timemarkket').innerHTML =''
            let timee = new Date()
            let pparticlec = document.createElement('p')
            pparticlec.innerHTML = `${timee.getHours()}:${timee.getMinutes()}:${timee.getSeconds()}`
            document.getElementById('timemarkket').appendChild(pparticlec)
        }
        setInterval(Timerr, 3);
    }
    keywordCloser(){
        if(document.getElementById('tarkov')){
            document.addEventListener('keydown',(e)=>{
                if(e.keyCode === 27){
                    document.getElementById('tarkov').classList.add('torinduboshit')
                }
            })

        }
       
    }

    funlogoutfrom(){
        if( document.getElementById('hide_button_oftimer')){
            document.getElementById('hide_button_oftimer').style.display = 'none'
        }
        if(document.getElementById('closerforhidden')){
            document.getElementById('closerforhidden').innerHTML = ''
        }
        
        document.getElementById('tarkov').innerHTML = ''
        document.getElementById('tarkov').classList.add('torinduboshit')
        this.props.mainService.getUserDetalis()
        .then(response=>{
         this.props.getingDay(0)
        })
       localStorage.removeItem('jwt')
       document.getElementById('classica').innerHTML =''
       document.getElementById('narmantia').style.display = 'flex'
       document.getElementById('homeclicker').click()
    }
    closerfunction(e){
        document.getElementById('roketstarts').style.display="none";
        document.getElementById('lolkek').classList.remove('ortographick');
        document.getElementById('svgcloser').style.display = 'none';
        document.getElementById('svgopen').style.display="block"
    }
    openfunctiri(){
        document.getElementById('lolkek').classList.add('ortographick');
     
        document.getElementById('svgcloser').style.display = 'block';
        document.getElementById('svgopen').style.display="none";
        setTimeout(() => {
            document.getElementById('roketstarts').style.display="block"; 
        }, 150);
    }
    togglecolorclass(e){
        let koko = document.querySelectorAll('.header_contaner_main__navbar__list li a svg')
        let roko = document.querySelectorAll('.header_contaner_main__navbar__list li a svg path')
        for(let i =0; i<koko.length; i++){
            roko[i].classList.remove('togglerclass')
            koko[i].classList.remove('togglerclass')
            koko[i].classList.add('mainhggher')
        }
        e.target.classList.remove('mainhggher')
        e.target.classList.add('togglerclass')
    }
    clickforntificationgroup(){
        document.getElementById('grouplinkers').click()
    }
    repushforhotfication(){
       
        
    }
    render(){
        return (
            <React.Fragment>
    <div className="main_header_menii">
        <div className="main_header_menii_left_side" id="lolkek">
        <div className="outergoung"><div id="svgcloser" className="svg_closer"><svg onClick={(e)=>{this.closerfunction(e)}} width="22" height="22" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fillRule="evenodd" clipRule="evenodd"><path d="M12 11.293l10.293-10.293.707.707-10.293 10.293 10.293 10.293-.707.707-10.293-10.293-10.293 10.293-.707-.707 10.293-10.293-10.293-10.293.707-.707 10.293 10.293z"/></svg></div></div>
        <div id="svgopen" className="svg_open"><svg onClick={(e)=>{this.openfunctiri(e)}} width="24" height="24" xmlns="http://www.w3.org/2000/svg" fillRule="evenodd" clipRule="evenodd"><path d="M24 18v1h-24v-1h24zm0-6v1h-24v-1h24zm0-6v1h-24v-1h24z" fill="#1040e2"/><path d="M24 19h-24v-1h24v1zm0-6h-24v-1h24v1zm0-6h-24v-1h24v1z"/></svg></div>
     
       <div id="roketstarts">
        <nav className="header_contaner_main__navbar">
                    <ul className="header_contaner_main__navbar__list">
                        <li>
                        <Link id="homeclicker"  to="/">
                        <svg className="togglerclass" onClick={(e)=>{this.togglecolorclass(e)}} width="42" height="36" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fillRule="evenodd" clipRule="evenodd"><path d="M22 11.414v12.586h-20v-12.586l-1.293 1.293-.707-.707 12-12 12 12-.707.707-1.293-1.293zm-6 11.586h5v-12.586l-9-9-9 9v12.586h5v-9h8v9zm-1-7.889h-6v7.778h6v-7.778z"/></svg>
                        </Link>
                        </li>
                        <li>
                        <Link to="/myToDo">
                        <svg className="mainhggher" onClick={(e)=>{this.togglecolorclass(e)}} width="42" height="36" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fillRule="evenodd" clipRule="evenodd"><path d="M12 0c6.623 0 12 5.377 12 12s-5.377 12-12 12-12-5.377-12-12 5.377-12 12-12zm0 1c6.071 0 11 4.929 11 11s-4.929 11-11 11-11-4.929-11-11 4.929-11 11-11zm7 7.457l-9.005 9.565-4.995-5.865.761-.649 4.271 5.016 8.24-8.752.728.685z"/></svg>
                        </Link>
                        </li>
                        <li>
                        <Link to="/helpus">
                        <svg className="mainhggher" onClick={(e)=>{this.togglecolorclass(e)}} width="42" height="36" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fillRule="evenodd" clipRule="evenodd"><path d="M12 0c6.623 0 12 5.377 12 12s-5.377 12-12 12-12-5.377-12-12 5.377-12 12-12zm0 1c6.071 0 11 4.929 11 11s-4.929 11-11 11-11-4.929-11-11 4.929-11 11-11zm.053 17c.466 0 .844-.378.844-.845 0-.466-.378-.844-.844-.844-.466 0-.845.378-.845.844 0 .467.379.845.845.845zm.468-2.822h-.998c-.035-1.162.182-2.054.939-2.943.491-.57 1.607-1.479 1.945-2.058.722-1.229.077-3.177-2.271-3.177-1.439 0-2.615.877-2.928 2.507l-1.018-.102c.28-2.236 1.958-3.405 3.922-3.405 1.964 0 3.615 1.25 3.615 3.22 0 1.806-1.826 2.782-2.638 3.868-.422.563-.555 1.377-.568 2.09z"/></svg>
                        </Link>
                        </li>
                        <li>
                        <Link id="grouplinkers" to="/groups">
                        <span>{this.props.datasik}</span>
                        <svg id="drswegger" className="mainhggher" onClick={(e)=>{this.togglecolorclass(e)}} xmlns="http://www.w3.org/2000/svg" width="42" height="36" viewBox="0 0 24 24" ><path d="M17.997 18h-.998c0-1.552.06-1.775-.88-1.993-1.438-.332-2.797-.645-3.293-1.729-.18-.396-.301-1.048.155-1.907 1.021-1.929 1.277-3.583.702-4.538-.672-1.115-2.707-1.12-3.385.017-.576.968-.316 2.613.713 4.512.465.856.348 1.51.168 1.908-.49 1.089-1.836 1.4-3.262 1.728-.982.227-.92.435-.92 2.002h-.995l-.002-.623c0-1.259.1-1.985 1.588-2.329 1.682-.389 3.344-.736 2.545-2.209-2.366-4.365-.676-6.839 1.865-6.839 2.492 0 4.227 2.383 1.867 6.839-.775 1.464.824 1.812 2.545 2.209 1.49.344 1.589 1.072 1.589 2.333l-.002.619zm4.81-2.214c-1.289-.298-2.489-.559-1.908-1.657 1.77-3.342.47-5.129-1.4-5.129-1.265 0-2.248.817-2.248 2.325 0 1.269.574 2.175.904 2.925h1.048c-.17-.75-1.466-2.562-.766-3.736.412-.692 1.704-.693 2.114-.012.38.631.181 1.812-.534 3.161-.388.733-.28 1.301-.121 1.648.305.666.977.987 1.737 1.208 1.507.441 1.368.042 1.368 1.48h.997l.002-.463c0-.945-.074-1.492-1.193-1.75zm-22.805 2.214h.997c0-1.438-.139-1.039 1.368-1.48.761-.221 1.433-.542 1.737-1.208.159-.348.267-.915-.121-1.648-.715-1.349-.914-2.53-.534-3.161.41-.682 1.702-.681 2.114.012.7 1.175-.596 2.986-.766 3.736h1.048c.33-.75.904-1.656.904-2.925.001-1.509-.982-2.326-2.247-2.326-1.87 0-3.17 1.787-1.4 5.129.581 1.099-.619 1.359-1.908 1.657-1.12.258-1.194.805-1.194 1.751l.002.463z"/></svg>
                        </Link>
                        </li>
                      
                    </ul>
                </nav>
                <div className="last_header_container">
                <ul>
                    <li>
                <Link to="/viberinfo">
                   <svg className="svg_screen_one" width="46" height="32" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fillRule="evenodd" clipRule="evenodd"><path d="M12.892 13.745s.427.038.656-.247l.448-.563c.216-.28.738-.458 1.249-.174.676.382 1.55.982 2.149 1.541.33.278.407.688.182 1.121l-.002.009c-.232.408-.541.791-.932 1.148l-.009.006c-.44.367-.946.58-1.487.404l-.01-.014c-.972-.275-3.304-1.464-4.79-2.649-2.431-1.918-4.159-5.082-4.637-6.778l-.015-.01c-.176-.543.039-1.049.404-1.488l.007-.008c.357-.391.739-.701 1.148-.932l.009-.002c.432-.225.842-.149 1.121.182.367.379 1.056 1.291 1.54 2.149.284.51.106 1.033-.173 1.248l-.564.448c-.284.23-.247.657-.247.657s.834 3.156 3.953 3.952zm4.907-2.616c-.167 0-.303-.135-.304-.302-.014-1.83-.564-3.288-1.634-4.332-1.072-1.045-2.427-1.581-4.027-1.592-.168-.001-.303-.138-.301-.306.001-.167.136-.301.303-.301h.002c1.762.012 3.258.606 4.447 1.764 1.19 1.162 1.802 2.765 1.817 4.763.001.167-.133.304-.301.306h-.002zm-1.595-.624h-.007c-.168-.004-.301-.143-.297-.31.024-1.038-.273-1.878-.906-2.569-.63-.689-1.495-1.065-2.645-1.149-.167-.013-.293-.158-.281-.325.013-.167.158-.293.325-.281 1.294.095 2.32.548 3.049 1.345.733.8 1.092 1.807 1.065 2.992-.004.165-.139.297-.303.297zm-1.558-.522c-.161 0-.295-.126-.303-.289-.051-1.03-.537-1.534-1.527-1.588-.168-.009-.296-.152-.287-.319.009-.168.151-.296.319-.287 1.308.07 2.034.819 2.101 2.164.009.167-.12.31-.288.318l-.015.001zm6.817 3.469c-.605 4.877-4.172 5.185-4.83 5.396-.28.09-2.882.737-6.152.524 0 0-2.438 2.94-3.199 3.705-.119.119-.258.167-.352.145-.131-.032-.167-.188-.165-.414l.02-4.016v-.001c-4.762-1.323-4.485-6.295-4.431-8.898.054-2.603.543-4.736 1.996-6.17 2.61-2.364 7.987-2.011 7.987-2.011 4.541.02 6.717 1.387 7.222 1.845 1.675 1.434 2.528 4.867 1.904 9.895zm-.652-11.113c-.597-.548-3.007-2.298-8.375-2.322 0 0-6.33-.382-9.416 2.45-1.718 1.718-2.322 4.231-2.386 7.348-.063 3.118-.146 8.958 5.484 10.542l.006.002-.004 2.416s-.035.979.609 1.179c.779.241 1.236-.502 1.981-1.304.408-.44.971-1.086 1.397-1.58 3.851.322 6.812-.417 7.149-.526.777-.253 5.177-.816 5.893-6.657.738-6.022-.358-9.83-2.338-11.548z"/></svg>
                   </Link>
                   </li>
                   <li>
                   <Link to="/whatsapinfo">
                      
                      <svg className="svg_screen_two" xmlns="http://www.w3.org/2000/svg"  width="46" height="32" viewBox="0 0 24 24"><path d="M.057 24l1.687-6.163c-1.041-1.804-1.588-3.849-1.587-5.946.003-6.556 5.338-11.891 11.893-11.891 3.181.001 6.167 1.24 8.413 3.488 2.245 2.248 3.481 5.236 3.48 8.414-.003 6.557-5.338 11.892-11.893 11.892-1.99-.001-3.951-.5-5.688-1.448l-6.305 1.654zm6.597-3.807c1.676.995 3.276 1.591 5.392 1.592 5.448 0 9.886-4.434 9.889-9.885.002-5.462-4.415-9.89-9.881-9.892-5.452 0-9.887 4.434-9.889 9.884-.001 2.225.651 3.891 1.746 5.634l-.999 3.648 3.742-.981zm11.387-5.464c-.074-.124-.272-.198-.57-.347-.297-.149-1.758-.868-2.031-.967-.272-.099-.47-.149-.669.149-.198.297-.768.967-.941 1.165-.173.198-.347.223-.644.074-.297-.149-1.255-.462-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.297-.347.446-.521.151-.172.2-.296.3-.495.099-.198.05-.372-.025-.521-.075-.148-.669-1.611-.916-2.206-.242-.579-.487-.501-.669-.51l-.57-.01c-.198 0-.52.074-.792.372s-1.04 1.016-1.04 2.479 1.065 2.876 1.213 3.074c.149.198 2.095 3.2 5.076 4.487.709.306 1.263.489 1.694.626.712.226 1.36.194 1.872.118.571-.085 1.758-.719 2.006-1.413.248-.695.248-1.29.173-1.414z"/></svg>
                 
                   </Link>
                   </li>
                   </ul>
                </div>
                </div>
        </div>
            <header className="header_contaner_main">
                <div className="header_contaner_main_top_content">
                <p className="header_contaner_main_par">
                  <a href="/"><img src={Logo} alt=""/></a>
                </p>
                <p id="arkvarden">
                  
                </p>
                
            <div id="userSignout">
            <p id="timemarkket"></p>
            <div id="classica">
            <div id="nossssaa">

            </div>
            <Link to='/groups'><div id="aaaason" onClick={()=>{this.repushforhotfication()}} className="classnotification_tenta kuker">
               <img src={Notificationn} alt=""/>
                
                <div id="anoka" >
                    {this.props.datasik}
                </div> 
            </div></Link>
            
            </div>
           <div className="kekafrerew" id="narmantia"> <Link to="/myToDo"> <button className ="header_contaner_main_top_content__login">
                      LOGIN
                    </button></Link>
                    <Link to="/myToDo"><button className="header_contaner_main_top_content__registration">
                     REGISTER
                    </button></Link></div>
                
                  
                    <div id="tarkov" className="dropdownmenu1 torinduboshit">
                        <div id="ermula">

                        </div>
                        <Link to="/profile"><div id="erevanino">

                        </div></Link>
                    </div>
                </div>
                </div>
                </header>
           
                </div>
            </React.Fragment>
        )
    }
}
const mapStateToProps =({datasik})=>{
    return {datasik}
}
const mapDispatchToProps ={
    getingDay
}
export default Hoc()(connect(mapStateToProps,mapDispatchToProps)(Header));