import React, { Component } from "react";
import "./Monthly.scss";
import Hoc from "../../hoc";
import ArrowLefter from "./img/arrowprev.png";
import ArrowRighter from "./img/arrownext.png";
import Calendar from "./img/calendarnik.png";
import $ from "jquery";
class Monthly extends Component {
  constructor(props) {
    super(props);
    this.state = {
      month: "",
      day: "",
      year: ""
    };
  }
  componentDidMount() {
    document.getElementById("chengerkek").checked = "true";
    let arrayMonth = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December"
    ];
    if (
      localStorage.getItem("jwt") !== null &&
      localStorage.getItem("jwt") !== "undefined"
    ) {
      let kurtka = 0;
      for (let i = 0; i < arrayMonth.length; i++) {
        if (
          arrayMonth[i] ==
          document.getElementById("arkvarden").innerHTML.split(" ")[2]
        ) {
          kurtka = i;
        }
      }
      this.props.mainService
        .postDateToTask(
          parseInt(
            document.getElementById("arkvarden").innerHTML.split(" ")[3]
          ),
          kurtka,
          parseInt(document.getElementById("arkvarden").innerHTML.split(" ")[1])
        )
        .then(response => {
          console.log(response.data);
          if (response.data.length == 0) {
            let petrol_station = document.createElement("p");
            petrol_station.innerHTML = "there is no task for today";
            petrol_station.classList.add("realwotldtext");
            document.getElementById("todaySingn").appendChild(petrol_station);
          } else {
            for (let i = 0; i < response.data.length; i++) {
              if (i < 3) {
                let task_div = document.createElement("div");
                task_div.classList.add("div_tasker_for_todat");
                let imgicon_task_div = document.createElement("div");
                let div_for_titleandtime = document.createElement("div");
                let title_tasktoday = document.createElement("p");
                let text_fortadoy = document.createElement("p");
                let time_whentodaydate = document.createElement("p");
                time_whentodaydate.innerHTML = response.data[i].time.slice(
                  16,
                  21
                );
                time_whentodaydate.classList.add("mytimeradntodat");
                imgicon_task_div.classList.add("circle_next_fortoday");
                div_for_titleandtime.classList.add("textusoner");
                title_tasktoday.innerHTML = response.data[i].title;
                title_tasktoday.classList.add("titletext_todaytaask");
                text_fortadoy.innerHTML = response.data[i].text;
                text_fortadoy.classList.add("texttext_todattasask");
                if (response.data[i].group != 0) {
                  this.props.mainService
                    .getgroupifo(response.data[i].group)
                    .then(response1 => {
                      let img_homepagerino = document.createElement("img");
                      img_homepagerino.src = `http://localhost:1338/${response1.data[0].image}`;
                      img_homepagerino.classList.add(
                        "image__react_tak_nerabotaen"
                      );
                      imgicon_task_div.appendChild(img_homepagerino);
                    });
                } else {
                  this.props.mainService.getUserDetalis().then(response1 => {
                    imgicon_task_div.innerHTML = response1.data.userdata.username[0].toUpperCase();
                  });
                }
                div_for_titleandtime.appendChild(title_tasktoday);
                div_for_titleandtime.appendChild(text_fortadoy);
                div_for_titleandtime.appendChild(time_whentodaydate);

                task_div.appendChild(imgicon_task_div);
                task_div.appendChild(div_for_titleandtime);
                // 123
                document.getElementById("todaySingn").appendChild(task_div);
              } else {
                let task_div = document.createElement("div");
                task_div.classList.add("div_tasker_for_todat");
                let imgicon_task_div = document.createElement("div");
                let div_for_titleandtime = document.createElement("div");
                let title_tasktoday = document.createElement("p");
                let text_fortadoy = document.createElement("p");
                let time_whentodaydate = document.createElement("p");
                time_whentodaydate.innerHTML = response.data[i].time.slice(
                  16,
                  21
                );
                time_whentodaydate.classList.add("mytimeradntodat");
                imgicon_task_div.classList.add("circle_next_fortoday");
                div_for_titleandtime.classList.add("textusoner");
                title_tasktoday.innerHTML = response.data[i].title;
                title_tasktoday.classList.add("titletext_todaytaask");
                text_fortadoy.innerHTML = response.data[i].text;
                text_fortadoy.classList.add("texttext_todattasask");
                if (response.data[i].group != 0) {
                  this.props.mainService
                    .getgroupifo(response.data[i].group)
                    .then(response1 => {
                      let img_homepagerino = document.createElement("img");
                      img_homepagerino.src = `http://localhost:1338/${response1.data[0].image}`;
                      img_homepagerino.classList.add(
                        "image__react_tak_nerabotaen"
                      );
                      imgicon_task_div.appendChild(img_homepagerino);
                    });
                } else {
                  this.props.mainService.getUserDetalis().then(response1 => {
                    imgicon_task_div.innerHTML = response1.data.userdata.username[0].toUpperCase();
                  });
                }
                div_for_titleandtime.appendChild(title_tasktoday);
                div_for_titleandtime.appendChild(text_fortadoy);
                div_for_titleandtime.appendChild(time_whentodaydate);

                task_div.appendChild(imgicon_task_div);
                task_div.appendChild(div_for_titleandtime);

                // 123
                document.getElementById("todohidentoday").appendChild(task_div);
              }
            }
            if (response.data.length > 3) {
              let button_todo_lister = document.createElement("button");
              button_todo_lister.innerHTML = "SHOW ALL";
              button_todo_lister.classList.add("buttoningtodoler");
              button_todo_lister.id = "hide_button_oftimer";
              button_todo_lister.addEventListener("click", () => {
                if (
                  document
                    .getElementById("todohidentoday")
                    .classList.contains("hidentogglertodo")
                ) {
                  document
                    .getElementById("todohidentoday")
                    .classList.remove("hidentogglertodo");
                  button_todo_lister.innerHTML = "HIDE ALL";
                  document.getElementById("buttoncanseringp").style.overflowY =
                    "scroll";
                } else {
                  document
                    .getElementById("todohidentoday")
                    .classList.add("hidentogglertodo");
                  button_todo_lister.innerHTML = "SHOW ALL";
                  document.getElementById("buttoncanseringp").style.overflowY =
                    "hidden";
                }
              });
              document
                .getElementById("buttoncanseringp")
                .appendChild(button_todo_lister);
            }
          }
        });
    }

    const {
      match: { params }
    } = this.props;
    let koter = params.monthId.split(" ");
    this.setState({
      month: koter[0],
      day: koter[1],
      year: koter[2]
    });
  }
  componentDidUpdate() {
    const { history } = this.props;

    if (
      localStorage.getItem("jwt") == null ||
      localStorage.getItem("jwt") == "undefined"
    ) {
      history.push("/regsig");
    }
    document.getElementById("daydorday").innerHTML = "";
    document.getElementById("monthlydayly").innerHTML = "";
    const datamontly = new Date();
    let array = [
      "Sunday",
      "Monday",
      "Tuesday",
      "Wednesday",
      "Thursday",
      "Friday",
      "Saturday"
    ];
    let arrayMonth = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec"
    ];

    let date1 = new Date(this.state.year, this.state.month);
    Date.prototype.daysInMonth = function() {
      return 33 - new Date(this.getFullYear(), this.getMonth(), 33).getDate();
    };

    let actualmonth = date1.daysInMonth();
    let actualmotnnum = parseInt(this.state.month);
    // console.log(actualmotnnum
    let actualyearnum = parseInt(this.state.year);
    let headhingmonth = document.createElement("p");
    let spaningoYear = document.createElement("span");
    const {
      match: { params }
    } = this.props;
    spaningoYear.classList.add("moyalobofer");
    spaningoYear.innerHTML = `${params.monthId.slice(5, 10)}`;
    headhingmonth.innerHTML = arrayMonth[actualmotnnum];

    if (actualmotnnum == datamontly.getMonth()) {
      headhingmonth.style.color = "#826796";
      spaningoYear.style.color = "#826796";
    }
    document.getElementById("monthlydayly").appendChild(headhingmonth);
    document.getElementById("monthlydayly").appendChild(spaningoYear);
    let dif = document.createElement("div");
    let table = document.createElement("table");
    table.classList.add("table_classiksa");
    var letters = [
      "#cbc4d7",
      "#e4e3ec",
      "#f2e5f1",
      "#d8eff4",
      "#ecf4dc",
      "#ffb2b2",
      "#ffe3e9",
      "#c6e3ff",
      "#ffc0cb",
      "#cfe1ab",
      "#82d1dd",
      "#CCCCFF",
      "#99CCFF",
      "#99FFCC",
      "#FF99CC",
      "#FFFFCC",
      "#FFCCFF"
    ];
    let tr = document.createElement("tr");

    for (let k = 0; k < array.length; k++) {
      let th = document.createElement("th");

      th.innerText += array[k].slice(0, 2);
      tr.appendChild(th);
    }
    table.appendChild(tr);
    for (let p = 1; p < 7; p++) {
      let tr = document.createElement("tr");
      tr.id = p;
      table.appendChild(tr);
    }
    dif.appendChild(table);

    let krokodil = tr.querySelectorAll("th");
    console.log();
    //   for(let u =0; u<1; u++){
    //       console.log(krokodil[u])
    //   }
    //     console.log(krokodil)
    let dayinmonthdk = 1;
    // Заполнение датами месяц

    for (let j = 1; j <= actualmonth; j++) {
      let ddaater = new Date(actualyearnum, actualmotnnum, j);
      let td = document.createElement("td");

      if (j == 1) {
        for (let m = 0; m < ddaater.getDay(); m++) {
          let td = document.createElement("td");
          td.style.background = "rgba(196, 196, 196, 0.36)";
          td.style.border = "1px solid lightgray";
          table.querySelectorAll("tr")[1].appendChild(td);
        }
      }

      if (ddaater.getDay() == 0) {
        dayinmonthdk++;
      }
      td.innerHTML = j;
      if (ddaater.getDay() == 0) {
        td.style.backgroundColor = krokodil[0].style.backgroundColor;
      }
      if (ddaater.getDay() == 1) {
        td.style.backgroundColor = krokodil[1].style.backgroundColor;
      }
      if (ddaater.getDay() == 2) {
        td.style.backgroundColor = krokodil[2].style.backgroundColor;
      }
      if (ddaater.getDay() == 3) {
        td.style.backgroundColor = krokodil[3].style.backgroundColor;
      }
      if (ddaater.getDay() == 4) {
        td.style.backgroundColor = krokodil[4].style.backgroundColor;
      }
      if (ddaater.getDay() == 5) {
        td.style.backgroundColor = krokodil[5].style.backgroundColor;
      }
      if (ddaater.getDay() == 6) {
        td.style.backgroundColor = krokodil[6].style.backgroundColor;
      }
      let trky = table.querySelectorAll("tr")[dayinmonthdk];
      trky.appendChild(td);

      this.props.mainService.postDatefromTask().then(Response => {
        const {
          match: { params }
        } = this.props;

        let arraymothlydays = [
          "Jan",
          "Feb",
          "Mar",
          "Apr",
          "May",
          "Jun",
          "Jul",
          "Aug",
          "Sep",
          "Oct",
          "Nov",
          "Dec"
        ];
        for (let l = 0; l < Response.data.length; l++) {
          if (
            Response.data[l].date.split(" ")[1] ==
              arraymothlydays[parseInt(params.monthId.split(" ")[0])] &&
            Response.data[l].date.split(" ")[3] ==
              params.monthId.split(" ")[2] &&
            Response.data[l].date.split(" ")[2] == j &&
            document.getElementById("monthlydayly").querySelector("p")
              .innerHTML == Response.data[l].date.split(" ")[1]
          ) {
            let p1 = document.createElement("div");
            p1.classList.add("circlerclassfortask");

            td.append(p1);
          }
        }
      });
      td.addEventListener("click", function(e) {
        document.querySelector(".modal_window").innerHTML = "";
        let div_for_notification = document.createElement("div");
        let close_button = document.createElement("button");
        let button_for_zametka = document.createElement('button')
        let button_for_task = document.createElement('button')
        button_for_zametka.innerHTML = 'zametki'
        button_for_task.innerHTML = 'taski'
        close_button.innerHTML = "close";

        div_for_notification.appendChild(close_button);
        div_for_notification.appendChild(button_for_zametka);
        div_for_notification.appendChild(button_for_task);
        button_for_task.addEventListener('click',()=>{
            
        if (localStorage.getItem("jwt") != null) {
          history.push(
            `/post/${
              e.target.innerText
            } ${actualyearnum} ${actualmotnnum} ${j} ${ddaater.getDay()}`
          );
        } else {
          history.push("/regsig");
        }
        })
        button_for_zametka.addEventListener('click',()=>{
          if (localStorage.getItem("jwt") != null) {
            history.push(
              `/zametka/${
                e.target.innerText
              } ${actualyearnum} ${actualmotnnum} ${j} ${ddaater.getDay()}`
            );
          } else {
            history.push("/regsig");
          }
        })

        close_button.addEventListener('click',()=>{
          $(div_for_notification).fadeOut();
          document.querySelector(".MainHelperforalldicvs").style.filter =
          "";
          
        })
        document.addEventListener('keydown',(e)=>{
          if(e.keyCode == 27){
            $(div_for_notification).fadeOut();
            document.querySelector(".MainHelperforalldicvs").style.filter =
            "";
          
          }
        })

        div_for_notification.classList.add("div_for_notiv_modal");
        document
          .querySelector(".modal_window")
          .appendChild(div_for_notification);
        $(div_for_notification).fadeIn();
        document.querySelector(".MainHelperforalldicvs").style.filter =
          "blur(5px)";

      });
      td.style.cursor = "pointer";
      // Текущий день
      if (j == datamontly.getDate() && actualmotnnum == datamontly.getMonth()) {
        td.style.color = "#826796";
        td.addEventListener("mouseover", e => {
          e.target.style.color = "#fff";
          e.target.style.background = "#826796";
        });
        td.addEventListener("mouseout", e => {
          e.target.style.background = "#F9F5F5";
          e.target.style.color = "#826796";
        });
      }
      // Текущий день
    }
    document.getElementById("daydorday").appendChild(dif);
  }
  async myPositivhu() {
    let newmonth12 = 0;
    console.log(this.state.month);
    if (this.state.month > 10) {
      await this.setState({
        month: `${newmonth12}`
      });
    } else {
      let newmonth = parseInt(this.state.month) + 1;
      this.setState({
        month: `${newmonth}`
      });
    }
  }
  myDicriments() {
    let newmonth12 = 11;
    if (this.state.month <= 0) {
      this.setState({
        month: `${newmonth12}`
      });
    } else {
      let newmonth = parseInt(this.state.month) - 1;
      this.setState({
        month: `${newmonth}`
      });
    }
  }
  changerfrommonthtoyear() {
    this.props.history.push("/");
  }
  render() {
    return (
      <React.Fragment>
        <style>
          {
            "body { background: linear-gradient(180deg, rgba(130, 103, 150, 0.15) 0%, rgba(232, 172, 113, 0.15) 100%) no-repeat; height:100vh;}"
          }
        </style>
        <div className="MainHelperforalldicvs">
          <div
            id="buttoncanseringp"
            className="MainHelperforalldicvs__todaystask"
          >
            <div className="MainHelperforalldicvs__todaystask_text01">
              <img width="40" src={Calendar} alt="" /> <p> Today’s Events</p>
            </div>
            <div className="marple_liner"></div>
            <div id="closerforhidden">
              <div id="todaySingn"></div>
              <div id="todohidentoday" className="hidentogglertodo"></div>
            </div>
          </div>

          <div className="helper">
            <label
              className="switch"
              onClick={() => {
                this.changerfrommonthtoyear();
              }}
            >
              <p className="perYear2">Year</p>
              <p className="perYear3">Month</p>
              <input id="chengerkek" type="checkbox" />
              <span className="slider round"></span>
            </label>
            <div className="secondSliderGeorge">
              <div>
                <button
                  onClick={() => {
                    this.myDicriments();
                  }}
                >
                  <img src={ArrowLefter} alt="" />
                </button>
              </div>

              <div id="monthlydayly"></div>
              <div>
                {" "}
                <button
                  onClick={() => {
                    this.myPositivhu();
                  }}
                >
                  <img src={ArrowRighter} alt="" />
                </button>
              </div>
            </div>

            <div id="daydorday"></div>
          </div>
        </div>
        <div class="modal_window"></div>
      </React.Fragment>
    );
  }
}
export default Hoc()(Monthly);
