import React,{Component} from 'react';
import './viberpag.scss';
import Code from './img/itemcode.png';
import Hoc from '../../hoc';


class Viberpag  extends Component{
    componentDidMount(){
        if(localStorage.getItem('jwt')==null || localStorage.getItem('jwt')== "undefined"){
            this.props.history.push('/regsig')
        }
        document.getElementById('tekercodergeeter').click()

    }
    submitterCode(){

            this.props.mainService.viberconnection()
            .then(response=>{
                console.log(response.data.token)
                if(response.data.token=="undefined" || response.data.token==null){
                    document.getElementById('codeclone').innerHTML = ''
                    let code = document.createElement('p')
                    code.innerHTML = "You have already connected viber"
                    code.classList.add('textupperlowdag3')
                    document.getElementById('codeclone').appendChild(code)
                }
                else{
                    let code = document.createElement('p')
                    code.innerHTML = response.data.token
                    code.classList.add('textupperlowdag')
                    document.getElementById('codeclone').innerHTML = ''
                    document.getElementById('codeclone').appendChild(code)
                }
              
            })
      
    }
    replacepossitiontowhat(e){
        e.preventDefault()
        this.props.history.push('/whatsapinfo')
    }
    repalcetohelpuspage(e){
        e.preventDefault()
        this.props.history.push('/helpus')
    }
    render(){
        return(
            <React.Fragment>
            <style>
          {
            "body { background: linear-gradient(180deg, rgba(130, 103, 150, 0.15) 0%, rgba(232, 172, 113, 0.15) 100%) no-repeat; height:100vh }"
          }
        </style>
                <p className="heading_viber_notification">Connect <span>Viber</span> notifications</p>
                <p className="heading_after_heading">Follow the three steps to receive notification in Viber</p>

                <div className="vibercontentnotic__flexcont">
                    <div className="vibercontentnotic__flexcont_first">
                        <p className="vibercontentnotic__flexcont_first_hex">About Viber Connectivity</p>
                        <p className="vibercontentnotic__flexcont_first_he">Simple connection procedure:</p>
                        <ul>
                         <li>
                            
In order to connect Viber, you must add to your contacts a special bot - SalGerBot. You can do this manually or using a QR code.

                         </li>
                         <li>
                            
Next, you need to bind your username to your Viber number. To do this, click on the button below and send the received code to the bot.
                          

                 </li>
         <li>
                   
If everything is entered correctly, then you will receive a corresponding message from the bot.   
                          </li>
             
                     </ul>
                     <p className="viber_finish_settings_end_left">If you didn’t succeed or have any problems, <a href="" onClick={(e)=>{this.repalcetohelpuspage(e)}}>please write to us*</a></p>
                    </div>

                    <div className="vibercontentnotic__flexcont_second">
                    <p className="vibercontentnotic__flexcont_first_hex">
Receive a verification code</p> 
                    <img src={Code} alt=""/>
                    <p className="viber_finish_settings">
Scan this code from your mobile device. To do this, go to Viber and select the QR option*</p>
<button id="tekercodergeeter" onClick={(e)=>{this.submitterCode(e)}}>Get the code</button>
<div id="codeclone">

                </div>
                <p className="viber_finish_settings"> Write the received code in a personal message to our bot*</p>
                <p className="viber_finish_settings_end">You can also connect <a href="" onClick={(e)=>{this.replacepossitiontowhat(e)}}>Whatsapp</a> notifications</p>
                    </div>    
                </div> 
                
            </React.Fragment>
        )
    }
}
export default Hoc()(Viberpag);