import React,{Component} from 'react';
import Erorin from '../erorindi';
class Erorb extends Component{
    constructor(props){
        super(props)
        this.state={
            hasEror:false
        }
    }
    componentDidCatch(){
        this.setState({
            hasEror:true
        })
    }
    render(){
        const {hasEror} = this.state;
        if(hasEror){
            return <Erorin/>
        }
        return this.props.children
    }
}
export default Erorb;