 const initialState = {
        datasik:0,
        admins:[],
        followers:[]
}
const reducer = (state=initialState,action)=>{
    switch (action.type) {
        case 'POST_DAY':
            return{
                ...state,
                datasik:action.payload
            }
        case 'ADMIN_PANEL_SET':
            return{
                ...state,
                admins:action.payload.admins,
                followers:action.payload.followers
            }
        default:
           return state;
    }
}
export default reducer;