import React, { Component } from "react";
import Hoc from "../../hoc";
import "./Helper.scss";
import { withStyles } from "@material-ui/core/styles";
import { Button } from "@material-ui/core";

class Helper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mail: "",
      message: ""
    };
  }
  onchangerhelpus(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
  }
  submithelpusform() {
    const { mainService } = this.props;
    mainService.postDataForHelpUs(this.state.mail, this.state.message);
  }
  render() {
    const { classes } = this.props;
    return (
      <React.Fragment>
        <style>
          {
            "body { background: linear-gradient(180deg, rgba(130, 103, 150, 0.15) 0%, rgba(232, 172, 113, 0.15) 100%) no-repeat; height:100vh; }"
          }
        </style>
        <form className="helperdagerinser">
          <p className="helperdagerinser__senedr">
            Help us make the <span>application better</span>
          </p>
          <p className="helperdagerinser__report">
            Report application errors or deficiencies in your message
          </p>
          <div className="input_label_emailer_div">
            <label className="input_label_emailer_custom" htmlFor="emailinputs">
              Email
            </label>
            <input
              name="mail"
              value={this.state.mail}
              onChange={e => {
                this.onchangerhelpus(e);
              }}
              id="emailinputs"
              type="email"
            />
          </div>

          <div className="input_label_emailer_div_message">
            <label
              className="input_label_emailer_custom"
              htmlFor="messagetaxt_area"
            >
              Message
            </label>
            <textarea
              name="message"
              value={this.state.message}
              onChange={e => {
                this.onchangerhelpus(e);
              }}
              placeholder="Your message here..."
              id="messagetaxt_area"
            ></textarea>
          </div>
          <Button onClick={()=>{this.submithelpusform()}} className={classes.root}>Send</Button>
        </form>

        {/* <input name="mail" value={this.state.mail} onChange={(e)=>{this.onchangerhelpus(e)}} type="email" placeholder="Email"/><br/>
                    <textarea name="message" value={this.state.message} onChange={(e)=>{this.onchangerhelpus(e)}} placeholder="Message">
                    </textarea><br/>
                    <button type="button" onClick={()=>{this.submithelpusform()}}>SEND</button> */}
      </React.Fragment>
    );
  }
}
const styles = theme => ({
  root: {
    width: "130px",
    height: "40px",
    background: "#826796",
    display: "block",
    marginTop: "20px",
    fontFamily: "Montserrat-Regular",
    color: "#fff"
  }
});
export default Hoc()(withStyles(styles)(Helper));
