import React,{Component} from 'react';
import Hoc from '../../hoc';
import './regis.scss';
import logoTypp from './img/logotype.png'
import {withStyles} from '@material-ui/core/styles';
import {Button} from '@material-ui/core'
class Regis extends Component{
    constructor(props){
        super(props)
        this.state={
        login:'',
        email:'',
        password:'',
        signlogin:'',
        signpassw:''   
        }
    }
    componentDidMount(){
        document.getElementById('freeclick').click()
    }
    serializingreg(e){
        this.setState({
            [e.target.name]:e.target.value
        })

       if(e.target.name === "login"){
           let reqs  = /[а-яё]/;
           let engreq = /[a-z]/
           let EngRes = /[A-Z]/
            if(e.target.value.length<8 || reqs.test(e.target.value) || !engreq.test(e.target.value) || !EngRes.test(e.target.value[0]) ){
                document.getElementById('validform1').innerHTML =''
                e.target.style.borderBottom = '1px solid tomato';
                let petro = document.createElement('p')
                petro.classList.add('toggler_classfor_valid')
                petro.innerHTML = 'You need to use: upper symbol, eng language and words more then 8'
                document.getElementById('validform1').appendChild(petro)
            }
            else{
                document.getElementById('validform1').innerHTML =''
                e.target.style.borderBottom = '1px solid lightgreen'
            }
       }
       else if(e.target.name === "email"){
           let kartofel = /[@]/;
           let kartofel1 = /[.]/;
           let reqs  = /[а-яё]/;
           let engreq = /[a-z]/
            if(!kartofel.test(e.target.value) || !kartofel1.test(e.target.value) || e.target.value.length<5 || reqs.test(e.target.value) || !engreq.test(e.target.value) ){
                document.getElementById('validform2').innerHTML =''
                e.target.style.borderBottom = '1px solid tomato';
                let petro = document.createElement('p')
                petro.classList.add('toggler_classfor_valid')
                petro.innerHTML = 'You need to use: "@" or ".", eng language and words more then 5'
                document.getElementById('validform2').appendChild(petro)
            }
            else{
                document.getElementById('validform2').innerHTML =''
                e.target.style.borderBottom = '1px solid lightgreen'
            }
        }
        else if(e.target.name === "password"){
            let reqs  = /[а-яё]/;
            let engreq = /[a-z]/;
            let numeric = /[0-9]/
            let EngRes = /[A-Z]/
            if(e.target.value.length<6 || reqs.test(e.target.value) || !engreq.test(e.target.value) || !numeric.test(e.target.value) || !EngRes.test(e.target.value[0])){
                document.getElementById('validform3').innerHTML =''
                e.target.style.borderBottom = '1px solid tomato';
                let petro = document.createElement('p')
                petro.classList.add('toggler_classfor_valid')
                petro.innerHTML = 'You need to use: upper symbol, some numbers, eng language and words more then 6'
                document.getElementById('validform3').appendChild(petro)
            }
            else{
                document.getElementById('validform3').innerHTML =''
                e.target.style.borderBottom = '1px solid lightgreen'
            }
        }
        else if(e.target.name === "signlogin"){
            let reqs  = /[а-яё]/;
            let engreq = /[a-z]/
            let EngRes = /[A-Z]/
             if(e.target.value.length<8 || reqs.test(e.target.value) || !engreq.test(e.target.value) || !EngRes.test(e.target.value[0]) ){
                 document.getElementById('validform4').innerHTML =''
                 e.target.style.borderBottom = '1px solid tomato';
                 let petro = document.createElement('p')
                 petro.classList.add('toggler_classfor_valid')
                 petro.innerHTML = 'You need to use: upper symbol, eng language and words more then 8'
                 document.getElementById('validform4').appendChild(petro)
             }
             else{
                 document.getElementById('validform4').innerHTML =''
                 e.target.style.borderBottom = '1px solid lightgreen'
             }
        }
        else if(e.target.name === "signpassw"){
            let reqs  = /[а-яё]/;
            let engreq = /[a-z]/;
            let numeric = /[0-9]/
            let EngRes = /[A-Z]/
            if(e.target.value.length<6 || reqs.test(e.target.value) || !engreq.test(e.target.value) || !numeric.test(e.target.value) || !EngRes.test(e.target.value[0])){
                document.getElementById('validform5').innerHTML =''
                e.target.style.borderBottom = '1px solid tomato';
                let petro = document.createElement('p')
                petro.classList.add('toggler_classfor_valid')
                petro.innerHTML = 'You need to use: upper symbol, some numbers, eng language and words more then 6'
                document.getElementById('validform5').appendChild(petro)
            }
            else{
                document.getElementById('validform5').innerHTML =''
                e.target.style.borderBottom = '1px solid lightgreen'
            }
        }
      
    }
    submitterreg(){
        const{
            mainService,history
        }=this.props
        let htmlcolle = document.getElementById('reqformone').querySelectorAll('input')
        for(let i =0; i<htmlcolle.length; i++){
            if(htmlcolle[i].style.borderBottom == '1px solid tomato'){
               htmlcolle[i].value = ''
               alert('Zapolnite pravilno polia')
          
               return
            }
            else if(htmlcolle[i].value.length == 0){
                htmlcolle[i].style.borderBottom = '1px solid tomato'
                
            }
            else{

        mainService.postUserReg(this.state.login,this.state.email,this.state.password)
        .then(response=>{
            alert('Пожалуйста пройдите варификацию на почте')
            document.getElementById('freeclick').click()
        })
      
             return
            }

        }
      
    }
    sumbitsigni(){
        const{
            mainService
        }=this.props
        mainService.posrUserSignin(this.state.signlogin,this.state.signpassw)
        .then(Response=>{
            localStorage.setItem('jwt',Response.data.token)
            if(localStorage.getItem('jwt')!==null){
                window.location.href = '/'
            }
        })
    }
    submitregistr(typreexit,e){
        let htmlcolle = document.getElementById('reqformone').querySelectorAll('input')
        for(let i =0; i<htmlcolle.length; i++){
            htmlcolle[i].value =''
        }
        let toobuttons  = document.querySelectorAll('.nullering');
        for(let i =0;i<toobuttons.length;i++){
            toobuttons[i].style.background ='none'
            toobuttons[i].style.color='#826796'
        }
        e.target.style.background ='linear-gradient(180deg, rgba(130, 103, 150, 0.84) 0.52%, rgba(130, 103, 150, 0.5124) 0.53%, rgba(219, 194, 170, 0.84) 90.1%)'
        e.target.style.color='white'
        let forms = document.querySelectorAll('.formerrefistr');
        for(let i=0;i<forms.length; i++){
            forms[i].style.display = "none"
        }
        document.getElementById(typreexit).style.display ="flex"
    }
    clicklinkreg(e){
        e.preventDefault()
        document.getElementById('payforclick').click()

    }
    clicksinginregafter(e){
        e.preventDefault()
        document.getElementById('freeclick').click()
    }
   
    render(){
        const {classes} = this.props
        return(
            <React.Fragment>
                 <style>{'body { background: linear-gradient(180deg, rgba(130, 103, 150, 0.15) 0%, rgba(232, 172, 113, 0.15) 100%) no-repeat; height:100vh;}'}</style>
                <p className="heading_author">authorization</p>
                <div className="middlerconsumer">
                    
                    <button className="nullering" id="payforclick" onClick ={(e)=>{this.submitregistr('registration',e)}}>
                        Registration   
                    </button>    
                    <button className="nullering" id="freeclick" onClick ={(e)=>{this.submitregistr('signiner',e)}}>
                        Sign in
                    </button>
                </div>    

                
                <div className="formerrefistr" id="registration">
                    <div className="formerrefistr__serire_seconds1">
                        <div className="formerrefistr__serire_seconds__kordo">
                            <img src={logoTypp} alt=""/>
                        </div>
                         <form id="reqformone" className="signinformcustomer">
                            <p className="formerrefistr__serire_petro_registr">REGISTER</p>
                            <div className="firstoinput_signin1">
                                 <label className="signinformcustomer__input_label_reg" htmlFor="">
                                 Username
                                 </label>
                                 <input className="signinformcustomer__input"  required type="text" value={this.state.login}  name="login" onChange={(e)=>{this.serializingreg(e)}}/>
                                 <div id="validform1"></div>
                             </div>
                             <div className="firstoinput_signin212">
                                 <label className="signinformcustomer__input_label_reg" htmlFor="">
                                 E - mail
                                 </label>
                                <input  className="signinformcustomer__input" required type="email" value={this.state.email} name="email" onChange={(e)=>{this.serializingreg(e)}}/><div id="validform2"></div>
                             </div>
                             <div className="firstoinput_signin212">
                                 <label className="signinformcustomer__input_label_reg" htmlFor="">
                                 Password
                                 </label>
                                <input  className="signinformcustomer__input" required type="password" value={this.state.password} name="password" onChange={(e)=>{this.serializingreg(e)}}/><div id="validform3"></div>
                             </div>
                             <button  className="signinformcustomer__button_regd" onClick={(e)=>{this.submitterreg(e)}} type="button">SIGNUP</button>
                             <p className="formerrefistr__serire_petro1_reg">
                             If you are already a registered user, please  <a onClick={(e)=>{this.clicksinginregafter(e)}} href="">log in</a></p>
                        </form>
                    </div>
                </div>
              
             
             <div className="formerrefistr" id="signiner">
                 
               <div className="formerrefistr__serire_seconds">
                <div className="formerrefistr__serire_seconds__kordo">
                    <img src={logoTypp} alt=""/>
                </div>
               <form className="signinformcustomer">
                   <p className="formerrefistr__serire_petro21">LOGIN</p>
                   <p className="formerrefistr__serire_petro12">TO CONTINUE</p>
                   <div className="firstoinput_signin1">
                       <label className="signinformcustomer__input_label" htmlFor="">
                       username
                   </label>
                   <input className="signinformcustomer__input"  type="text" value={this.state.signlogin} name="signlogin" onChange={(e)=>{this.serializingreg(e)}}/>
                   <div id="validform4"></div>
                   </div>
                   <div className="firstoinput_signin2">
                   <label className="signinformcustomer__input_label" htmlFor="">
                       password
                   </label>
                   <input  className="signinformcustomer__input" type="password" value={this.state.signpassw} name="signpassw" onChange={(e)=>{this.serializingreg(e)}}/> <div id="validform5"></div>
                   <p className="linkin_forgotpassword"><a href="">Forget your password ?</a></p>
                   </div>
                   {/* <button className="signinformcustomer__button" type="button" onClick={(e)=>{this.sumbitsigni(e)}}>LOGIN</button> */}
                   <Button  className={classes.root} onClick={(e)=>{this.sumbitsigni(e)}} >LOGIN</Button>
                   <p className="formerrefistr__serire_petro1">
If you are not registered, please go through the <a onClick={(e)=>{this.clicklinkreg(e)}} href="">registration step</a> and try again</p>
                 
               </form>
               </div>
             </div>
           
            </React.Fragment>
        )
    }
}
const styles= (theme) => ({
    root:{
        width:'110px',
        maxWidth:'100%',
        height:'37px',
        color: '#F9F5F5',
        border:'none',
        fontFamily: 'Montserrat-Regular',
        background: '#826796',
        borderRadius: '16px',
        fontSize:'14px',
        display: 'block',
        margin:'30px auto 0px auto',
        transition: 'all .5s ease',
            '&:hover':{
                transition: 'all .5s ease',
                background:'#fff',
                color:'#826796'
            }
    }
})
export default Hoc()(withStyles(styles)(Regis));