import React,{Component} from 'react';
import './varify.scss';
import Hoc from '../../hoc';

class Varify extends Component{
    componentDidMount(){
       const{
           match:{params},mainService
       }=this.props
       console.log(params.token)
       mainService.varifyget(params.token)
       .then(response=>{
         setTimeout(() => {
            this.props.history.push('/regsig')
         }, 2000);  
       })
    }
    render(){
        return(
            <React.Fragment>
                    <div id="successverify">
                        
You have successfully completed the modification. Continue to enter. Welcome<br/>
<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M12 0c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm-1.25 16.518l-4.5-4.319 1.396-1.435 3.078 2.937 6.105-6.218 1.421 1.409-7.5 7.626z"/></svg>
                    </div>
            </React.Fragment>
        )
    }

}
export default Hoc()(Varify);