import React,{Component} from 'react';
import './whatsap.scss';
import Hoc from '../../hoc';
class Whatsapp extends Component{
    componentDidMount(){
        if(localStorage.getItem('jwt')==null || localStorage.getItem('jwt')== "undefined"){
            this.props.history.push('/regsig')
        }
        document.getElementById('redsocee').click()
    }
    getcodefrowhatsapp(){
        this.props.mainService.whatsappconnection()
        .then(response=>{
            if(response.data.token=="undefined" || response.data.token==null){
                document.getElementById('forwhatsappcode').innerHTML =''
                let code = document.createElement('p')
                code.innerHTML = "You have already connected viber"
                code.classList.add('textupperlowdag4')
                document.getElementById('forwhatsappcode').appendChild(code)
            }
            else{
                let code = document.createElement('p')
                code.innerHTML = response.data.token
                code.classList.add('textupperlowdag1')
                document.getElementById('forwhatsappcode').innerHTML =''
                document.getElementById('forwhatsappcode').appendChild(code)
            }
         
        })
    }
    repalcetohelpuspage(e){
        e.preventDefault()
        this.props.history.push('/helpus')
    }
    replacepossitiontoviber(e){
        e.preventDefault()
        this.props.history.push('/viberinfo')
    }
    render(){
        return(
            <React.Fragment>
                 <style>
          {
            "body { background: linear-gradient(180deg, rgba(130, 103, 150, 0.15) 0%, rgba(232, 172, 113, 0.15) 100%) no-repeat; height:100vh }"
          }
        </style>
                   <p className="heading_whatsapp_notification">Connect <span>Whatsapp</span> notifications</p>
                   <p className="heading_after_heading">Follow the three steps to receive notification in Whatsapp</p>
                   <div className="vibercontentnotic__flexcont">
                    <div className="whatsappcontentnotic__flexcont_first">
                        <p className="vibercontentnotic__flexcont_first_hex">About Whatsapp Connectivity</p>
                        <p className="vibercontentnotic__flexcont_first_he">Simple connection procedure:</p>
                        <ul>
                         <li>In order to connect Whatsapp, you need to add your number to the sandbox Trillo, for this write the code word "join any-key" to +1 415 523 8886.</li>
                         <li>
                            Then you should receive the following message. Next, you need to bind your username to your Whatsapp number. To do this, click on the button below and send the received code to the same number.
                        </li>
                         <li>
                         If everything is entered correctly, then you will receive the appropriate message about the successful connection.
                        </li>
                     </ul>
                     <p className="viber_finish_settings_end_left">If you didn’t succeed or have any problems, <a href="" onClick={(e)=>{this.repalcetohelpuspage(e)}}>please write to us*</a></p>
                    </div>
                    <div className="whatsappcontentnotic__flexcont_second">
                    <p className="vibercontentnotic__flexcont_first_hex">Receive a verification code</p> 
                    <p className="whatsapp_finish_settings">
                    After you wrote a personal message at +1 415 523 8886. You will need to write the received code*</p>
                    <button id="redsocee" onClick={()=>{this.getcodefrowhatsapp()}}>Get the code</button> 
                  <div id="forwhatsappcode">
                    </div>
                <p className="whatspp_finish_settings_end">You can also connect <a href="" onClick={(e)=>{this.replacepossitiontoviber(e)}}>Viber</a> notifications</p>
                    </div>    
                </div> 
            </React.Fragment>
        )
    }
}
export default Hoc()(Whatsapp);