import React from 'react';
import {MyConsumer} from '../context';
const Hoc = ()=>(Wrapped)=>{
    return(props)=>{
        return(
            <MyConsumer>
                {
                    (mainService)=>{
                       return (<Wrapped {...props} mainService={mainService} />) 
                    }
                }
            </MyConsumer>
        )
    }
}
export default Hoc;