import React, { Component } from "react";
import socketIOClient from "socket.io-client";
import "./App.scss";
import { Switch, Route } from "react-router-dom";
import HomePage from "../pages/homePage";
import Day from "../pages/Day";
import Header from "../pages/header";
import MyTodo from "../pages/myTodo";
import Regis from "../pages/signinregistru";
import Viberpag from "../pages/viberpage";
import Whatsapp from "../pages/whatsapppage";
import Varify from "../pages/varify";
import Monthly from "../pages/Monthly";
import Helper from "../pages/helper";
import Groups from "../pages/groups";
import Group from "../pages/group";
import Profile from "../pages/profildatae";
import Taskone from "../pages/task";
import Zametka from '../pages/zametka';
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      endpoint: "localhost:1338"
    };
  }
  componentDidMount() {
    const { history } = this.props;
    if (
      localStorage.getItem("jwt") !== null &&
      localStorage.getItem("jwt") !== "undefined"
    ) {
      let tokenn = JSON.parse(atob(localStorage.getItem("jwt").split(".")[1]));
      const socket = socketIOClient(this.state.endpoint);
      socket.emit("join", tokenn._id);
      socket.on("mess", col => {
        alert(col);
      });
      socket.on("invite", coler => {
        let confe = window.confirm("Vas pozvali v gruppu pereity>?");
        if (confe) {
          window.location.href = "/groups";
        }
      });
    }
  }
  render() {
    return (
      <React.Fragment>
        <Header />
        <Switch>
          <Route path="/" exact component={HomePage} />
          <Route path="/post/:dayID" component={Day} />
          <Route path="/myToDo" component={MyTodo} />
          <Route path="/regsig" component={Regis} />
          <Route path="/viberinfo" component={Viberpag} />
          <Route path="/whatsapinfo" component={Whatsapp} />
          <Route path="/verify/:token" component={Varify} />
          <Route path="/monthly/:monthId" component={Monthly} />
          <Route path="/helpus" component={Helper} />
          <Route path="/groups" component={Groups} />
          <Route path="/group/:groupId" component={Group} />
          <Route path="/profile" component={Profile} />
          <Route path="/task/:taskId" component={Taskone} />
          <Route path="/zametka/:zametkaId" component={Zametka}/>
        </Switch>
      </React.Fragment>
    );
  }
}

export default App;
