import React, { Component } from "react";
import { Link } from "react-router-dom";
import Hoc from "../../hoc";
import { connect } from "react-redux";
import "./Day.scss";
import closer from "./img/closer.png";
import Pancel from "./img/pancil3.png";
import Success from "./img/success.png";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import { withStyles } from "@material-ui/core/styles";
import { Switch, Button } from "@material-ui/core";
import $ from "jquery";

class Day extends Component {
  constructor(props) {
    super(props);
    this.state = {
      array121: [],
      datetext: "",
      datetime: "",
      title: "",
      level: "hight",
      loading: true,
      viber: false,
      whatsap: false,
      email: false,
      changeviber: false,
      changewhatsapp: false,
      idcheck: 1,
      timeisnow: new Date() + "",
      checkedB: false,
      group_ide: "",
      group_namer: ""
    };
  }
  componentDidUpdate() {
    if (this.state.checkedB) {
      $(".groyp_task_selector__group").fadeIn();
    } else {
      $(".groyp_task_selector__group").fadeOut();
    }
  }
  componentDidMount() {
    this.setState({
      datetime: this.state.timeisnow
    });
    const { mainService } = this.props;
    mainService.getUserDetalis().then(response => {
      console.log(response.data.userdata.groups);
      {
        /* <ul className="roolerq"> */
      }
      let counter = 0;
      for (let i = 0; i < response.data.userdata.groups.length; i++) {
        let lishkaforul = document.createElement("li");
        let labelabel = document.createElement("label");
        let labelforul = document.createElement("label");
        let label_span = document.createElement("span");
        label_span.innerHTML = response.data.userdata.groups[i].group_name;
        lishkaforul.id = response.data.userdata.groups[i]._id;
        let checkbox_item = document.createElement("input");
        checkbox_item.type = "radio";
        lishkaforul.classList.add("checkbox");
        checkbox_item.id = `checkbox_${counter + 1}`;
        checkbox_item.name = `checkbox`;
        labelabel.htmlFor = `checkbox_${counter + 1}`;
        counter++;
        checkbox_item.addEventListener("change", e => {
          this.setState({
            group_ide: e.target.parentNode.parentNode.id,
            group_namer: e.target.parentNode.querySelector("span").innerHTML
          });
          // console.log(e.target.parentNode.parentNode.id);
          // console.log(e.target.parentNode.querySelector("span").innerHTML);
        });

        // let checkbox_for = document.createElement(Checkbox);

        labelforul.appendChild(checkbox_item);
        labelforul.appendChild(labelabel);
        labelforul.appendChild(label_span);
        lishkaforul.appendChild(labelforul);

        document.querySelector(".roolerq").appendChild(lishkaforul);
      }
    });

    let arrayMonth = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December"
    ];
    let arrayDayWeek = [
      "Sunday",
      "Monday",
      "Tuesday",
      "Wednesday",
      "Thursday",
      "Friday",
      "Saturday"
    ];
    const {
      match: { params }
    } = this.props;
    // Take dayOfWeek

    let all_Date_from_url = params.dayID.split(" ");
    let dayof_week = arrayDayWeek[parseInt(all_Date_from_url[4])];
    let currentmonth = arrayMonth[parseInt(all_Date_from_url[2])];
    // Take dayOfWeek

    // Picked data for task
    // Add dayo of week
    let span_for_dayofweek = document.createElement("span");
    span_for_dayofweek.innerHTML = dayof_week;
    document.getElementById("cleathex").appendChild(span_for_dayofweek);
    // Add dayo of week

    // Add day of month
    let span_for_dayofmonth = document.createElement("span");
    span_for_dayofmonth.innerHTML = ` ${all_Date_from_url[0]}`;
    document.getElementById("cleathex").appendChild(span_for_dayofmonth);
    // Add day of month

    // Add month
    let span_for_month = document.createElement("span");
    span_for_month.innerHTML = ` ${currentmonth}`;
    document.getElementById("cleathex").appendChild(span_for_month);
    // Add month

    // Add Year
    let span_for_year = document.createElement("span");
    span_for_year.innerHTML = ` ${all_Date_from_url[1]}`;
    document.getElementById("cleathex").appendChild(span_for_year);
  }
  async handleDateChange(date) {
    await this.setState({
      timeisnow: date,
      datetime: date + ""
    });
    console.log(this.state.datetime.split(" "));
  }
  changer(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
    if (e.target.tagName == "SELECT") {
    } else {
      if (e.target.value.length === 0) {
        e.target.style.background = "#F0EAED";
      } else {
        e.target.style.background = "#fff";
      }
    }
  }

  handleChange(e) {
    this.setState({
      checkedB: !this.state.checkedB
    });
  }
  changerBoxWhatsapp(e) {
    this.setState({
      whatsap: e.target.checked
    });
  }
  changerBoxViber(e) {
    this.setState({
      viber: e.target.checked
    });
  }
  addTaskforList() {
    const {
      mainService,
      match: { params },
      history
    } = this.props;
    // console.log(params.dayID.split(" ")[0]);
    // // Day
    // console.log(params.dayID.split(" ")[2]);
    // // Month
    // console.log(params.dayID.split(" ")[1]);
    // // Year
    // console.log(this.state.datetext);
    // // Text
    // console.log(this.state.title);
    // // Title
    // console.log(this.state.level);
    // // Level
    // console.log(this.state.datetime.slice(16, 21));
    // // time
    // console.log(this.state.viber)
    // console.log(this.state.whatsap)
    // console.log(this.state.group_ide)
    // console.log(this.state.group_namer)

    mainService.postData(
      params.dayID.split(" ")[1],
      params.dayID.split(" ")[2],
      params.dayID.split(" ")[0],
      this.state.datetext,
      this.state.datetime.slice(16, 21),
      this.state.title,
      this.state.level,
      this.state.viber,
      this.state.whatsap,
      this.state.group_ide
    );
    history.push("/myToDo");
  }
  render() {
    const { classes } = this.props;
    return (
      <React.Fragment>
        <style>
          {
            "body { background: linear-gradient(180deg, rgba(130, 103, 150, 0.15) 0%, rgba(232, 172, 113, 0.15) 100%) no-repeat; height:auto; padding-bottom:133px; }"
          }
        </style>

        <div className="flexcreator_todo_list">
          <p id="cleathex" className="paragraph_for_date"></p>

          <form className="maxanika">
            <div className="maxanika_div_name">
              <label className="create_task_label">Name</label>
              <input
                name={"title"}
                value={this.state.title}
                onChange={e => {
                  this.changer(e);
                }}
                className="create_task_name"
                type="text"
                placeholder="Title for your task"
              />
            </div>
            <div className="maxanika_div_name_text">
              <label className="create_task_label">Text</label>
              <textarea
                name={"datetext"}
                value={this.state.datetext}
                onChange={e => {
                  this.changer(e);
                }}
                className="create_task_textarea"
                type="text"
                placeholder="Text for your task"
              ></textarea>
            </div>
            <div className="maxanika_div_name_text">
              <label className="create_task_label">Time</label>
              <MuiPickersUtilsProvider
                className={classes.kook}
                utils={DateFnsUtils}
              >
                <KeyboardTimePicker
                  className={classes.root}
                  id="time-picker"
                  label="Time picker"
                  value={this.state.timeisnow}
                  onChange={date => {
                    this.handleDateChange(date);
                  }}
                  KeyboardButtonProps={{
                    "aria-label": "change time"
                  }}
                />
              </MuiPickersUtilsProvider>
              <p className="text_notification">Task notification time</p>
            </div>
            <div className="maxanika_div_name_text">
              <label className="create_task_label">Priority</label>
              <select
                className="create_task_select"
                type="text"
                placeholder="Text for your task"
                value={this.state.level}
                name="level"
                onChange={e => {
                  this.changer(e);
                }}
              >
                <option selected="selected" disabled>
                  Task priority
                </option>
                <option value="high">High</option>
                <option value="medium">Medium</option>
                <option value="low">Low</option>
              </select>
            </div>
            <div className="maxanika_div_name_checkboxing">
              <label className="create_task_label">Notice</label>
              <div className="maxanika_div_name_checkboxing_div">
                <p className="discription_for_notice">
                  Where do you want to receive notifications ?
                </p>
                <FormControlLabel
                  className={classes.rootCheckbox}
                  control={
                    <Checkbox
                      name="whatsap"
                      value={this.state.whatsap}
                      onChange={e => {
                        this.changerBoxWhatsapp(e);
                      }}
                    />
                  }
                  label="Whatsapp"
                />
                <FormControlLabel
                  className={classes.rootCheckbox}
                  control={
                    <Checkbox
                      name="viber"
                      value={this.state.viber}
                      onChange={e => {
                        this.changerBoxViber(e);
                      }}
                    />
                  }
                  label="Viber"
                />
                <p className="discription_for_notice">
                  To connect notifications in Viber and WhatsApp you need to
                  connect them first. Go to the menu item and follow the
                  instructions. Connect with Viber. WhatsApp
                </p>
              </div>
            </div>
            <div className="maxanika_div_name_text">
              <label className="create_task_label_groyop">Groups</label>
              <div className="groyp_task_selector">
                <label htmlFor="">Create for group </label>
                <Switch
                  checked={this.state.checkedB}
                  onChange={e => {
                    this.handleChange("");
                  }}
                  value="checkedB"
                  color="primary"
                  inputProps={{ "aria-label": "primary checkbox" }}
                  className={classes.rootSwitch}
                />
                <div className="groyp_task_selector__group">
                  <div className="groyp_task_selector__group_kniga">
                    Choose group
                  </div>
                  <ul className="roolerq"></ul>
                </div>
              </div>
            </div>

            <Button
              className={classes.rootbuttonm}
              onClick={() => {
                this.addTaskforList();
              }}
            >
              create
            </Button>
          </form>
        </div>
      </React.Fragment>
    );
  }
}
const styles = theme => ({
  rootbuttonm: {
    textTransform: "uppercase",
    color: "#826796",
    border: "1px solid #826796",
    display: "block",
    margin: "30px auto 0px auto",
    fontSize: "13px",
    width: "100px",
    background: "none",
    transition: "all .5s ease",
    "&:hover": {
      background: "#826796",
      color: "#fff",
      transition: "all .5s ease"
    }
  },
  rootSwitch: {},
  root: {
    marginLeft: 50,
    "& label": {
      fontFamily: "Montserrat-Regular",
      fontSize: "16px"
    },
    "& button": {
      color: "#826796"
    },
    "& input": {
      fontFamily: "Montserrat-Regular",
      fontSize: "15px"
    }
  },
  rootCheckbox: {
    "& svg": {
      width: "20px",
      height: "20px"
    },
    "& span": {
      fontFamily: "Montserrat-Regular",
      color: "#333",
      fontSize: "15px",
      lineHeight: "35px"
    }
  }
});
const mapStateToProps = ({ datasik }) => {
  return { datasik };
};
export default Hoc()(connect(mapStateToProps)(withStyles(styles)(Day)));
