  createrArticle() {
    document.getElementById("allofmy_todo_user").innerHTML = "";
    document.querySelector(".circilar_Progresstype").style.display = "block";
    setTimeout(() => {
      document.querySelector(".circilar_Progresstype").style.display = "none";
      const { mainService } = this.props;
      mainService.postDatefromTask().then(response => {
        console.log(response);
        if (response.data.length === 0) {
          let warning_message = document.createElement("div");
          warning_message.classList.add("warning_no_info");
          warning_message.innerHTML = "You have not any task";
          document
            .getElementById("allofmy_todo_user")
            .appendChild(warning_message);
        } else {
          for (let i = 0; i < response.data.length; i++) {
            // Main__block_article
            let main_article = document.createElement("article");
            main_article.id = response.data[i]._id;
            main_article.classList.add("article_mytodo");
            // Main__block_article

            // Article__content_number
            let article_number = document.createElement("div");
            let article_number_content = document.createElement("p");
            article_number_content.innerHTML = `#${i + 1}`;
            article_number.appendChild(article_number_content);
            main_article.appendChild(article_number);
            // Article__content_number

            // Article+_content_title
            let article_title = document.createElement("div");
            let article_title_content = document.createElement("p");
            article_title_content.innerHTML = response.data[i].title;
            article_title.appendChild(article_title_content);
            main_article.appendChild(article_title);
            // Article+_content_title

            // Article_content_text
            let article_text = document.createElement("div");
            let article_text_content = document.createElement("p");
            article_text_content.innerHTML = response.data[i].text;
            article_text.appendChild(article_text_content);
            main_article.appendChild(article_text);
            // Article_content_text

            // Article_group_n
            let article_group = document.createElement("div");
            let article_group_content = document.createElement("p");
            if (response.data[i].group.length > 0) {
              // Take_group_adressssss
              mainService
                .getgroupifo(response.data[i].group)
                .then(respondegr => {
                  article_group_content.innerHTML =
                    respondegr.data[0].group_name;
                });
              // Take_group_adressssss
            } else {
              article_group_content.innerHTML = "None";
              article_group_content.classList.add("article_none_group");
            }
            article_group.appendChild(article_group_content);
            main_article.appendChild(article_group);
            // Article_group_n

            // Article_date
            let article_date = document.createElement("div");
            let article_date_content = document.createElement("p");
            article_date_content.innerHTML = response.data[i].date.slice(3, 15);
            article_date.appendChild(article_date_content);
            main_article.appendChild(article_date);
            // Article_date

            // Article_time
            let article_time = document.createElement("div");
            let article_time_content = document.createElement("p");
            article_time_content.innerHTML = response.data[i].time.slice(
              15,
              21
            );
            article_time.appendChild(article_time_content);
            main_article.appendChild(article_time);
            // Article_time

            // Article_status
            let article_status = document.createElement("div");
            let article_status_content = document.createElement("button");
            // Status_article
            if (response.data[i].status === "waiting") {
              article_status_content.classList.add("waiting_button");
              article_status_content.innerHTML = "waiting";
            } else if (response.data[i].status === "success") {
              article_status_content.classList.add("success_button");
              article_status_content.innerHTML = "success";
            }
            article_status.appendChild(article_status_content);
            main_article.appendChild(article_status);
            // Status_article
            // Artucle_status

            // Control_elements
            let article_element_control = document.createElement("div");
            let buttons_element_control = document.createElement("aside");
            buttons_element_control.classList.add("element_control_div");
            let article_element_control_img = document.createElement("img");
            let article_element_control_img_pen = document.createElement("img");
            article_element_control_img_pen.src = `${Pencil}`;
            article_element_control_img.src = `${Garbage}`;
            article_element_control_img.addEventListener("click", e => {
              // Delete_element
              let conf = window.confirm("действительно удалить?");
              if (conf) {
                e.target.parentNode.parentNode.parentNode.remove();
                mainService.postDeleteTask(
                  e.target.parentNode.parentNode.parentNode.id
                );
                if (
                  document.getElementById("allofmy_todo_user").children
                    .length === 0
                ) {
                  let warning_message = document.createElement("div");
                  warning_message.classList.add("warning_no_info");
                  warning_message.innerHTML = "You have not any task";
                  document
                    .getElementById("allofmy_todo_user")
                    .appendChild(warning_message);
                }
              }
              // Delete_element
            });
            article_element_control_img_pen.addEventListener("click", (e) => {
              this.props.history.push(`/task/${e.target.parentNode.parentNode.parentNode.id}`)
            });
            buttons_element_control.appendChild(
              article_element_control_img_pen
            );
            buttons_element_control.appendChild(article_element_control_img);
            article_element_control.appendChild(buttons_element_control);
            main_article.appendChild(article_element_control);
            // Control_elements

            document
              .getElementById("allofmy_todo_user")
              .appendChild(main_article);
          }
        }
      });
    }, 500);
  }
