import React, { Component } from "react";
import "./task.scss";
import Hoc from "../../hoc";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker
} from "@material-ui/pickers";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import { Switch, Button, withStyles } from "@material-ui/core";
import DateFnsUtils from "@date-io/date-fns";

class Taskone extends Component {
  constructor(props){
    super(props)
    this.state={
      title:'',
      datetext:'',
      timeisnow: new Date() + "",
      level: "hight",
      viber: false,
      whatsap: false,
    }
  }


  componentDidMount() {
    const {
      match: { params },
      mainService
    } = this.props;
    mainService.oneTaskInfo(params.taskId).then(response => {
      console.log(response)
      if(response.data[0].status == "waiting"){
        this.setState({
          title:response.data[0].title,
          datetext:response.data[0].text,
          timeisnow:response.data[0].time,
          level:response.data[0].level,
          
        })
        if(response.data[0].viber == true){
          document.getElementById('viberino').click()
        }
        if(response.data[0].whatsapp == true){
          document.getElementById('whatsapino').click()
        }
      }
      else{
       document.getElementById('place_for_data').innerHTML = ''
      let  p_for_nothing = document.createElement('p')
      p_for_nothing.innerHTML = 'This task is alredy done'
      p_for_nothing.classList.add('p_for_nothing')
      document.getElementById('place_for_data').appendChild(p_for_nothing)
      }
    });
  }
  changer(e){
    this.setState({
      [e.target.name]:e.target.value
    })
  }
  async handleDateChange(date) {
    await this.setState({
      timeisnow: date,
    });
  }
  changerBoxWhatsapp(e) {
    this.setState({
      whatsap: e.target.checked
    });
  }
  changerBoxViber(e) {
    this.setState({
      viber: e.target.checked
    });
  }
  addTaskforList(){
    const {match:{params},mainService} = this.props
    // console.log(params.taskId)
    // console.log(this.state.viber)
    // console.log(this.state.whatsap)
    // console.log(this.state.title)
    // console.log(this.state.datetext)
    // console.log(this.state.timeisnow)

    mainService.postChangeTask(this.state.title,this.state.datetext,this.state.timeisnow, params.taskId,this.state.viber,this.state.whatsap)
    .then(response=>{
      console.log(response)
    })
    
  }
  render() {
    const {
      match: { params },
      classes
    } = this.props;

    return (
      <>
      <div id="place_for_data">


        <form className="maxanika">
            <div className="maxanika_div_name">
              <label className="create_task_label">Name</label>
              <input
                name={"title"}
                value={this.state.title}
                onChange={e => {
                  this.changer(e);
                }}
                className="create_task_name"
                type="text"
                placeholder="Title for your task"
              />
            </div>
            <div className="maxanika_div_name_text">
              <label className="create_task_label">Text</label>
              <textarea
                name={"datetext"}
                value={this.state.datetext}
                onChange={e => {
                  this.changer(e);
                }}
                className="create_task_textarea"
                type="text"
                placeholder="Text for your task"
              ></textarea>
            </div>
            <div className="maxanika_div_name_text">
              <label className="create_task_label">Time</label>
              <MuiPickersUtilsProvider
                className={classes.kook}
                utils={DateFnsUtils}
              >
                <KeyboardTimePicker
                  className={classes.root}
                  id="time-picker"
                  label="Time picker"
                  value={this.state.timeisnow}
                  onChange={date => {
                    this.handleDateChange(date);
                  }}
                  KeyboardButtonProps={{
                    "aria-label": "change time"
                  }}
                />
              </MuiPickersUtilsProvider>
              <p className="text_notification">Task notification time</p>
            </div>
            <div className="maxanika_div_name_text">
              <label className="create_task_label">Priority</label>
              <select
                className="create_task_select"
                type="text"
                placeholder="Text for your task"
                value={this.state.level}
                name="level"
                onChange={e => {
                  this.changer(e);
                }}
              >
                <option selected="selected" disabled>
                  Task priority
                </option>
                <option value="high">High</option>
                <option value="medium">Medium</option>
                <option value="low">Low</option>
              </select>
            </div>
            <div className="maxanika_div_name_checkboxing">
              <label className="create_task_label">Notice</label>
              <div className="maxanika_div_name_checkboxing_div">
                <p className="discription_for_notice">
                  Where do you want to receive notifications ?
                </p>
                <FormControlLabel
                  className={classes.rootCheckbox}
                  control={
                    <Checkbox
                      id="whatsapino"
                      name="whatsap"
                      value={this.state.whatsap}
                      onChange={e => {
                        this.changerBoxWhatsapp(e);
                      }}
                    />
                  }
                  label="Whatsapp"
                />
                <FormControlLabel
                  className={classes.rootCheckbox}
                  control={
                    <Checkbox
                      id="viberino"
                      name="viber"
                      value={this.state.viber}
                      onChange={e => {
                        this.changerBoxViber(e);
                      }}
                    />
                  }
                  label="Viber"
                />
                <p className="discription_for_notice">
                  To connect notifications in Viber and WhatsApp you need to
                  connect them first. Go to the menu item and follow the
                  instructions. Connect with Viber. WhatsApp
                </p>
              </div>
            </div>
       

            <Button
              className={classes.rootbuttonm}
              onClick={() => {
                this.addTaskforList();
              }}
            >
              create
            </Button>
          </form>
          </div>
      </>
    );
  }
}
const styles = theme => ({
  rootbuttonm: {
    textTransform: "uppercase",
    color: "#826796",
    border: "1px solid #826796",
    display: "block",
    margin: "30px auto 0px auto",
    fontSize: "13px",
    width: "100px",
    background: "none",
    transition: "all .5s ease",
    "&:hover": {
      background: "#826796",
      color: "#fff",
      transition: "all .5s ease"
    }
  },
  rootSwitch: {},
  root: {
    marginLeft: 50,
    "& label": {
      fontFamily: "Montserrat-Regular",
      fontSize: "16px"
    },
    "& button": {
      color: "#826796"
    },
    "& input": {
      fontFamily: "Montserrat-Regular",
      fontSize: "15px"
    }
  },
  rootCheckbox: {
    "& svg": {
      width: "20px",
      height: "20px"
    },
    "& span": {
      fontFamily: "Montserrat-Regular",
      color: "#333",
      fontSize: "15px",
      lineHeight: "35px"
    }
  }
});
export default Hoc()(withStyles(styles)(Taskone));
